
@tailwind base;
@tailwind components;
@tailwind utilities;

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  margin: 0;
}

@layer base {
  :root {
    --background: #ffffff;
    --foreground: #1c1c1e;

    --primary-background: #ebf0ff;

    --card: #ffffff;
    --card-foreground: #1c1c1e;

    --popover: #ffffff;
    --popover-foreground: #1c1c1e;

    --primary: #1c1c1e;
    --primary-foreground: #ffffff;

    --secondary: #d1e8ff;
    --secondary-foreground: #1c1c2d;

    --muted: #d1e8ff;
    --muted-foreground: #717882;

    --accent: rgba(209, 232, 255, 0.2);
    --accent-foreground: #1c1c2d;

    --destructive: #f75255;
    --destructive-foreground: #d1e8ff;

    --border: #e4eaf0;
    --input: #e4eaf0;
    --ring: #1c1c1e;

    --radius: 8px;
  }

